<template>
  <nav class="bg-green-800">
    <div class="max-w-7xl mx-auto px-2 sm:px-6 lg:px-8">
      <div class="relative flex items-center justify-between h-16">
        <div class="absolute inset-y-0 left-0 flex items-center sm:hidden">
          <!-- Mobile menu button-->
          <button
            @click="mobileMenuOpen = !mobileMenuOpen"
            type="button"
            class="inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-white hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white"
            aria-controls="mobile-menu"
            aria-expanded="false"
          >
            <span class="sr-only">Open main menu</span>
            <svg
              class="block h-6 w-6"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              aria-hidden="true"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M4 6h16M4 12h16M4 18h16"
              />
            </svg>
            <svg
              class="hidden h-6 w-6"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              aria-hidden="true"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
        </div>
        <div
          class="flex-1 flex items-center justify-center sm:items-stretch sm:justify-start"
        >
          <div class="flex-shrink-0 flex items-center">
            <img
              class="mx-auto h-10 w-auto"
              src="../assets/images/pharmacy.png"
            />
          </div>
          <div class="hidden sm:block sm:ml-6">
            <div class="flex space-x-4">
              <div v-for="menu in menuData" :key="menu.title">
                <div
                  :class="currentRout == menu.route ? activeClass : btnClass"
                  @click="menuClick(menu.route)"
                >
                  {{ menu.title }}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          class="absolute inset-y-0 right-0 items-center pr-2 sm:static sm:inset-auto sm:ml-6 sm:pr-0 hidden sm:block"
        >
          <div :class="btnLogoutClass" @click="menuClick(menuLogOut.route)">
            {{ menuLogOut.title }}
          </div>
        </div>
      </div>
    </div>

    <!-- Mobile menu, show/hide based on menu state. -->
    <div
      class="sm:hidden"
      id="mobile-menu"
      :class="{ hidden: !mobileMenuOpen }"
    >
      <div class="px-2 pt-2 pb-3 space-y-1">
        <div v-for="menu in menuData" :key="menu.title">
          <div
            :class="currentRout == menu.route ? activeClass : btnClass"
            @click="menuClick(menu.route)"
          >
            {{ menu.title }}
          </div>
        </div>

        <div :class="btnLogoutClass" @click="menuClick(menuLogOut.route)">
          {{ menuLogOut.title }}
        </div>
      </div>
    </div>
  </nav>
  <Confirm
    v-if="showConfirm"
    icon="warning"
    title="ออกจากระบบ"
    message="กรุณายืนยันก่อนออกจากระบบ"
    btnCancel="ยกเลิก"
    btnOkay="ตกลง"
    @accepted="confirmDismiss"
  />
</template>

<script>
import router from "@/router";
import store from "@/store";
import { ref } from "@vue/reactivity";
import Confirm from "@/components/Confirm.vue";

export default {
  components: { Confirm },
  mounted() {
    console.log("mounted!");
    console.log(navigator.onLine);
  },
  setup() {
    const menuData = ref([
      {
        title: "หน้าหลัก",
        route: "/home",
      },
      {
        title: "ระบบขาย",
        route: "/order",
      },
      {
        title: "รายการสินค้า",
        route: "/stock",
      },
      {
        title: "ปรับ Stock",
        route: "/stock-adjust",
      },
      {
        title: "ค้นหาสินค้า",
        route: "/search",
      },
      {
        title: "รายการสั่งยา",
        route: "/drug-order",
      },
      {
        title: "คำนวณยาเด็ก",
        route: "/drug-calculate",
      },
      {
        title: "รายงาน",
        route: "/report",
      },
      {
        title: "ลูกค้า",
        route: "/member",
      },
      {
        title: "บริษัทคู๋ค้า",
        route: "/company",
      },
    ]);

    const menuLogOut = ref({
      title: "ออกจากระบบ",
      route: "/",
    });

    const currentRout = ref(router.currentRoute.value.path);
    const showConfirm = ref(false);
    const mobileMenuOpen = ref(false);

    const btnClass = ref(
      "text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-green-700 hover:text-white"
    );
    const activeClass = ref(
      "bg-gray-900 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-gray-900 hover:text-white"
    );
    const btnLogoutClass = ref(
      "bg-red-900 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-red-700 hover:text-white"
    );

    const menuClick = (path) => {
      if (path == "/") {
        showConfirm.value = true;
      } else {
        router.push(path);
      }
    };

    const confirmDismiss = (res) => {
      showConfirm.value = false;
      console.log(res);
      if (res) {
        store.dispatch("auth/ACTION_SET_AUTHENTICATION", false);
        router.push("/");
      }
    };

    return {
      menuData,
      menuLogOut,
      menuClick,
      activeClass,
      btnClass,
      btnLogoutClass,
      currentRout,
      showConfirm,
      confirmDismiss,
      mobileMenuOpen,
    };
  },
};
</script>

