import HttpRequest from "./http_request";
import Config from "../config/Config";

class UserProvider extends HttpRequest {
  constructor() {
    super("http://localhost/api_drugstore/services");
  }

  async getDrugOrders(branch) {
    const uri =
      branch == 1
        ? Config.globalVariables.branch1
        : Config.globalVariables.branch2;
    const { data } = await this.get(uri + "/pharmacy/api/list_pre_order.php");
    return data;
  }

  async getSales(branch, type, currentDate) {
    const uri =
      branch == 1
        ? Config.globalVariables.branch1
        : Config.globalVariables.branch2;

    console.log(uri);
    const { data } = await this.get(
      uri +
        "/pharmacy/api/report.php?var_report_type=" +
        type +
        "&var_by_date=" +
        currentDate
    );
    return data;
  }

  async getUser() {
    const { data } = await this.get("/jobs");
    return data;
  }

  async getMembers() {
    const { data } = await this.get("/getProducts.php");
    return data;
  }

  async addMember(params) {
    const { status } = await this.create("/members", params);
    return status;
  }

  async deleteMember(id) {
    const { status } = await this.delete("/members/" + id);
    return status;
  }

  async getMemberById(id) {
    const { data } = await this.get("/members/" + id);
    return data;
  }

  async editMember(id, params) {
    const { status } = await this.update("/members/" + id, params);
    return status;
  }
}

export default UserProvider;
