import { createRouter, createWebHistory } from "vue-router";
import Login from "@/views/Login.vue";
import Home from "@/views/Home.vue";
import Order from "@/views/Order.vue";
import Stock from "@/views/Stock.vue";
import Search from "@/views/Search.vue";
import Report from "@/views/Report.vue";
import Members from "@/views/members/Members.vue";
import Company from "@/views/Company.vue";
import NotFound from "@/views/NotFound.vue";
import AddMember from "@/views/members/AddMember.vue";
import EditMember from "@/views/members/EditMember.vue";
import DrugCalculate from "@/views/DrugCalculate.vue";
import DrugOrder from "@/views/DrugOrder.vue";
import StockAdjust from "@/views/StockAdjust.vue";
import store from "@/store";

const checkAuthenticated = (to, from, next) => {
  if (store.getters["auth/GET_AUTHENTICATED"] == false) {
    next("/");
  } else {
    next();
  }
};

const clearAllStateAuthenticated = () => {
  store.dispatch("auth/ACTION_CLEAR_AUTHENTICATION");
  store.dispatch("counter/ACTION_CLEAR_COUNTER");
};

const routes = [
  {
    path: "/",
    name: "Login",
    component: Login,
    beforeEnter: clearAllStateAuthenticated,
  },
  {
    path: "/home",
    name: "Home",
    component: Home,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/order",
    name: "Order",
    component: Order,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/stock",
    name: "Stock",
    component: Stock,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/search",
    name: "Search",
    component: Search,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/report",
    name: "Report",
    component: Report,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/member",
    name: "Members",
    component: Members,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/company",
    name: "Company",
    component: Company,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/add-member",
    name: "AddMember",
    component: AddMember,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/edit-member",
    name: "EditMember",
    component: EditMember,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/drug-calculate",
    name: "DrugCalculate",
    component: DrugCalculate,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/drug-order",
    name: "DrugOrder",
    component: DrugOrder,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/stock-adjust",
    name: "StockAdjust",
    component: StockAdjust,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/all-order",
    redirect: "/order",
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/:catchAll(.*)",
    name: "NotFound",
    component: NotFound,
    beforeEnter: checkAuthenticated,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
