<template>
  <NavBar />
  <div class="py-8 px-10">
    <div class="md:grid md:grid-cols-5 md:gap-6">
      <div class="md:col-span-1">
        <div class="px-4 sm:px-0">
          <div
            class="flex flex-col w-full md:w-64 text-gray-700 bg-white dark-mode:text-gray-200 dark-mode:bg-gray-800 flex-shrink-0"
            x-data="{ open: false }"
          >
            <div
              class="flex-shrink-0 px-8 py-4 flex flex-row items-center justify-between"
            >
              <a
                href="#"
                class="text-lg font-semibold tracking-widest text-gray-900 uppercase rounded-lg dark-mode:text-white focus:outline-none focus:shadow-outline"
                >รายการสั่งยา 2 สาขา</a
              >
            </div>
            <nav
              :class="{ block: open, hidden: !open }"
              class="flex-grow md:block px-4 pb-4 md:pb-0 md:overflow-y-auto"
            >
              <a
                class="block px-4 py-2 mt-2 text-sm font-semibold text-gray-900 bg-gray-200 rounded-lg dark-mode:bg-gray-700 dark-mode:hover:bg-gray-600 dark-mode:focus:bg-gray-600 dark-mode:focus:text-white dark-mode:hover:text-white dark-mode:text-gray-200 hover:text-gray-900 focus:text-gray-900 hover:bg-gray-200 focus:bg-gray-200 focus:outline-none focus:shadow-outline"
                href="#"
                >สาขาศาลายา 21 รายการ</a
              >
              <a
                class="block px-4 py-2 mt-2 text-sm font-semibold text-gray-900 bg-transparent rounded-lg dark-mode:bg-transparent dark-mode:hover:bg-gray-600 dark-mode:focus:bg-gray-600 dark-mode:focus:text-white dark-mode:hover:text-white dark-mode:text-gray-200 hover:text-gray-900 focus:text-gray-900 hover:bg-gray-200 focus:bg-gray-200 focus:outline-none focus:shadow-outline"
                href="#"
                >สาขาคาสเดย์ {{ orders.amount }} รายการ</a
              >
            </nav>
          </div>
        </div>
      </div>
      <div class="md:col-span-4">
        <button
          @click="openModalOrderDrugs()"
          class="absolute right-20 bg-gray-500 hover:bg-red-700 text-white text-sm px-3 py-2 rounded"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="w-6 h-6 inline-block"
            viewBox="0 0 20 20"
            fill="currentColor"
          >
            <path d="M8 3a1 1 0 011-1h2a1 1 0 110 2H9a1 1 0 01-1-1z" />
            <path
              d="M6 3a2 2 0 00-2 2v11a2 2 0 002 2h8a2 2 0 002-2V5a2 2 0 00-2-2 3 3 0 01-3 3H9a3 3 0 01-3-3z"
            />
          </svg>
          Copy & paste
        </button>
        <br />
        <ListDrugOrders :data="orders.results" :amount="orders.amount" />
      </div>
    </div>
  </div>
  <OrderDialog
    v-if="showModal"
    @closePressed="closeModal"
    :orders="modalData"
    color="bg-red-400"
  />
</template>

<script>
import NavBar from "@/components/NavBar";
import { ref, onMounted } from "vue";
import UserProvider from "@/resources/user_provider";
import ListDrugOrders from "@/components/ListDrugOrders";
import OrderDialog from "@/components/OrderDialog";
export default {
  components: { NavBar, ListDrugOrders, OrderDialog },
  setup() {
    let orders = ref({
      results: "",
      amount: 0,
    });
    const modalData = ref("");
    const showModal = ref(false);

    onMounted(() => {
      // getDrugOrders(1);
      getDrugOrders(2);
    });

    const getDrugOrders = async (branch) => {
      const userService = new UserProvider();
      const data = await userService.getDrugOrders(branch);
      orders.value.results = data.results;
      orders.value.amount = data.count;
    };

    const closeModal = () => {
      showModal.value = false;
    };

    const openModalOrderDrugs = () => {
      modalData.value = JSON.parse(JSON.stringify(orders.value.results));
      let tempText = [];
      for (let i = 0; i < modalData.value.length; i++) {
        let order =
          i +
          1 +
          ") " +
          modalData.value[i].trade_name +
          " จำนวน = " +
          modalData.value[i].order_amount +
          " " +
          modalData.value[i].unit;
        tempText.push(order);
      }
      console.log(tempText.join("\n"));
      modalData.value = tempText.join("\n");
      showModal.value = true;
    };

    return {
      getDrugOrders,
      orders,
      showModal,
      closeModal,
      openModalOrderDrugs,
      modalData,
    };
  },
};
</script>

