const initState = () => {
  return {
    count: 0,
  };
};

const state = initState();

const getters = {
  GET_COUNT(state) {
    return state.count;
  },
};

const mutations = {
  CHANGE_COUNT(state, value) {
    state.count = value;
  },
  CLEAR_COUNT(state) {
    Object.assign(state, initState());
  },
};

const actions = {
  ACTION_INCREASE_COUNT(context) {
    context.commit("CHANGE_COUNT", context.getters.GET_COUNT + 1);
  },
  ACTION_DECREASE_COUNT(context) {
    context.commit("CHANGE_COUNT", context.getters.GET_COUNT - 1);
  },
  ACTION_CLEAR_COUNTER(context) {
    context.commit("CLEAR_COUNT");
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
