const initState = () => {
  return {
    authenticated: false,
  };
};

const state = initState();

const getters = {
  GET_AUTHENTICATED(state) {
    return state.authenticated;
  },
};

const mutations = {
  CHANGE_AUTHENTICATION(state, status) {
    state.authenticated = status;
  },
  CLEAR_AUTHENTICATION(state) {
    Object.assign(state, initState());
  },
};

const actions = {
  ACTION_SET_AUTHENTICATION(context, status) {
    context.commit("CHANGE_AUTHENTICATION", status);
  },
  ACTION_CLEAR_AUTHENTICATION(context) {
    context.commit("CLEAR_AUTHENTICATION");
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
